.container {
    width: 885px;
    margin: 0 auto;
    display: flex;
}

.box {
    display: flex;
}

.form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 24px;
}

.form div {
    width: 100%;
    text-align: right;
}

.buttons {
    display: flex;
}

.button {
    display: flex;
}

.description {
    width: 250px;
}
